@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap);
html {
  font-size: 10px;
}

body {
  margin: 0;
}

* {
  box-sizing: border-box;
  outline: none;
}



.game-start{display:flex;align-items:center;width:100%;height:100vh}.game-start_bg{background:linear-gradient(to top left, #fff3eb 50%, #fff 50%)}.game-start__wrapper{display:flex;align-items:center;justify-content:space-between;width:110rem;margin:0 auto}.game-start__title{font-family:Inter;font-size:5.6rem;font-style:normal;font-weight:600;line-height:6.5rem;max-width:52.5rem;margin-top:0;margin-bottom:6.5rem}.game-start__score-title{font-family:Inter;font-size:3.2rem;font-style:normal;font-weight:600;line-height:3.7rem;color:#97979b;margin:0}.game-start__button{font-family:Inter;font-size:2rem;font-style:normal;font-weight:600;line-height:2.3rem;text-align:center;color:#ffffff;background-color:#ff8b37;border:none;border-radius:1.2rem;padding:2rem 12.3rem;transition:all 0.3s}.game-start__button:hover{background-color:#ffac70}.game-start__button:active{background-color:#e87928}

.game-field{background:#e5e5e5;height:100vh}.game-field__wrapper{display:flex;justify-content:space-between;height:100%}.game-field__question{display:flex;flex-direction:column;justify-content:space-between;flex:0 0 75%;padding:10.5rem 8rem 8.5rem}.game-field__title{font-family:Inter;font-size:3.2rem;font-style:normal;font-weight:600;line-height:3.7rem;max-width:70rem}.game-field__answers{display:flex;flex-wrap:wrap}.game-field__answer{width:39rem;margin:0 1.5rem 3.5rem}.game-field__steps{display:flex;background:#ffffff;flex:0 0 25%;overflow-x:hidden;padding:5rem 0;justify-content:flex-start}.game-field__steps-list{display:flex;width:100%;height:100%;align-items:center;justify-content:center;flex-direction:column;background:#ffffff;overflow-x:hidden}.game-field__step{font-family:Inter;font-size:2rem;font-style:normal;font-weight:400;line-height:2.3rem;text-align:center;width:24rem;padding:0.9rem 1rem 0.8rem;position:relative;background-size:cover}.game-field__step:not(:last-child){margin-bottom:0.8rem}.game-field__step:before,.game-field__step:after{content:"";display:block;width:100%;top:calc(50% - 0.1rem);position:absolute;height:0.1rem;background-color:#d0d0d8}.game-field__step:before{left:-100%}.game-field__step:after{right:-100%}.game-field__step_inactive{background-image:url(../../static/media/step-inactive.2bd1f67a.svg);color:#1C1C21}.game-field__step_inactive:before,.game-field__step_inactive:after{background-color:#d0d0d8}.game-field__step_current{background-image:url(../../static/media/step-current.e3708608.svg);color:#ff8b37}.game-field__step_current:before,.game-field__step_current:after{background-color:#ff8b37}.game-field__step_finished{background-image:url(../../static/media/step-finished.2bd1f67a.svg);color:#d0d0d8}.game-field__step_finished:before,.game-field__step_finished:after{background-color:#d0d0d8}.answer-button{font-family:Inter;font-size:2rem;font-style:normal;font-weight:400;line-height:2.3rem;background-size:cover;padding:2.6rem 3.3rem;background-image:url(../../static/media/answer-button-inactive.157d72db.svg);text-align:left;position:relative;border:none;background-color:transparent;transition:all 0.3s}.answer-button__letter{color:#ff8b37;margin-right:1rem}.answer-button:before,.answer-button:after{content:"";display:block;width:1.7rem;top:calc(50% - 0.1rem);position:absolute;height:0.1rem;background-color:#d0d0d8}.answer-button:before{left:-1.7rem}.answer-button:after{right:-1.7rem}.answer-button:hover{background-image:url(../../static/media/answer-button-hover.226e9422.svg)}.answer-button:hover:before,.answer-button:hover:after{background-color:#ff8b37}.answer-button_correct,.answer-button_correct:hover{background-image:url(../../static/media/answer-button-correct.29122387.svg)}.answer-button_correct:before,.answer-button_correct:after,.answer-button_correct:hover:before,.answer-button_correct:hover:after,.answer-button_correct:hover:before,.answer-button_correct:hover:after,.answer-button_correct:hover:hover:before,.answer-button_correct:hover:hover:after{background-color:#47d867}.answer-button_wrong,.answer-button_wrong:hover{background-image:url(../../static/media/answer-button-wrong.9948cae0.svg)}.answer-button_wrong:before,.answer-button_wrong:after,.answer-button_wrong:hover:before,.answer-button_wrong:hover:after,.answer-button_wrong:hover:before,.answer-button_wrong:hover:after,.answer-button_wrong:hover:hover:before,.answer-button_wrong:hover:hover:after{background-color:#ec6259}@media (max-width: 767px){.game-field__question{width:100%}.game-field__steps{position:fixed;width:100%}.mobile-hidden{display:none}}

.mobile-menu{position:absolute;top:20px;right:20px}.mobile-menu__button{border:none;padding:0}@media (min-width: 768px){.mobile-menu{display:none}}

